import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { User } from "../../models/user";
import { AngularFireAuth } from "angularfire2/auth/auth";
import { FirebaseServiceProvider } from "../../providers/firebase-service/firebase-service";
import { Storage } from "@ionic/storage";
import { TabsControllerPage } from "../tabs-controller/tabs-controller";
import { LoadingController } from 'ionic-angular';
import * as firebase from 'firebase/app';

@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html'
})
export class SignupPage {
  user = {} as User;
  error;
  // this tells the tabs component which Pages
  // should be each tab's root Page
  constructor(
    private afAuth: AngularFireAuth,
    public navCtrl: NavController,
    public firebaseService: FirebaseServiceProvider,
    public loadingCtrl: LoadingController) {
  }
  async register(user: User) {
    try {
      var loader = this.loadingCtrl.create({
        content: "Registering...",
        dismissOnPageChange: true
      });
      loader.present();
      firebase.database().ref("organization/" + user.organization_ID).once('value').then((snapshot) => {
        if (snapshot.val()) {
          this.afAuth.auth.createUserWithEmailAndPassword(user.email, user.password).then((res) => {
            this.user.uid = res.uid;
            this.user.password = '';
            this.firebaseService.addUserDetails(user).then(()=> {
              this.navCtrl.setRoot(TabsControllerPage);
            });
          });
        } else {
          throw new Error("Invalid organization code");
        }
      }).catch((e: Error) => {
        var trimmedMessage = /^.*:\s*(.*)$/.exec(e.message);
        if (trimmedMessage == null) {
          this.error = e.message;
        } else {
          this.error = trimmedMessage[1];
        }
      });

    } finally {
      loader.dismiss();
    }
  }

  goToLogin(params) {
    if (!params) params = {};
    this.navCtrl.setRoot(LoginPage, {}, { animate: true, direction: 'back' });
  }
}



